import React from "react";
import TransitionLink from "gatsby-plugin-transition-link";
import Img from "gatsby-image";
import parse from "html-react-parser";

export default class CreateTiles extends React.PureComponent {
  data;
  tileImgs = [];
  loadTiles = [];
  fadeTiming = 0.7;
  newLoads = 8;
  state = {
    tileLimit: 32,
  };

  constructor(props) {
    super();

    this.data = props.data;
    this.data = this.data.filter(el => el !== undefined);

    this.data.forEach(({ previewTile }) => {
      const { imageFile } = previewTile[0];
      const sharpImg = imageFile !== null ? imageFile.childImageSharp : null;

      let tileImgProps = {};

      if (sharpImg !== null) {
        tileImgProps.fluid = { ...sharpImg.fluid };
      } else {
        console.log(previewTile[0].url); // see which ones can't be converted to webp for testing

        tileImgProps.fluid = { src: previewTile[0].url };
      }

      const backgroundColor =
        imageFile && imageFile.colors !== null ? imageFile.colors.muted : "#efefef";

      tileImgProps.backgroundColor = backgroundColor;

      this.tileImgs.push(tileImgProps);
    });
  }

  componentDidMount() {
    this.mainEl = document.querySelector("main");
    this.loadMore();
  }

  loadMore(e) {
    if (e) {
      e.preventDefault();
    }

    this.data.forEach((tileData, i) => {
      if (i < this.state.tileLimit) {
        this.loadTiles[i] = tileData;
      }
    });

    this.setState({
      tileLimit: this.state.tileLimit + this.newLoads,
    });
  }

  tilesFadeOut(href) {
    const clicked = document.querySelector(`.work-tile[href*="${href}"]`);
    clicked.style.transition = `opacity ${this.fadeTiming / 2}s`;
    clicked.style.transitionDelay = `${this.fadeTiming / 2}s`;

    this.mainEl.classList.add("-pageFade");
  }

  render() {
    return (
      <React.Fragment>
        <div className="work-tile__container">
          {this.loadTiles.slice(0, this.state.tileLimit).map((data, i) => {
            const href = `/work/articles/${data.slug}`;

            return (
              <TransitionLink
                exit={{
                  length: this.fadeTiming,
                  trigger: () => this.tilesFadeOut(href),
                }}
                entry={{
                  delay: this.fadeTiming * 1.5,
                  length: this.fadeTiming,
                }}
                className="work-tile"
                key={`home tile ${i}`}
                to={href}
              >
                <Img
                  className={"work-tile__image aspect__2-by-1 hover-cursor"}
                  {...this.tileImgs[i]}
                />
                <p className="work-tile__client fontSize-p3 fontSize-uppercase">{data.title}</p>
                <span className="work-tile__title fontSize-p2">
                  {parse(data.description.content)}
                </span>
              </TransitionLink>
            );
          })}
        </div>

        {this.data.length > this.loadTiles.length ? (
          <div className="work-tile__load-more">
            <p
              onClick={e => {
                this.loadMore(e);
              }}
              className="work-tile__load-more-button fontSize-p2"
            >
              Load more work
            </p>
          </div>
        ) : (
          <div className="work-tile__load-more" />
        )}
      </React.Fragment>
    );
  }
}
