import React from "react";
import { graphql } from "gatsby";

import Layout from "../components/common/layout";
import CreateTiles from "../components/common/createTiles";

import WorkNav from "../components/work/workNav";
import filterArticles from "../components/work/filterArticles";
import findTags from "../components/work/findTags";

import "../styles/work.scss";

export default class Work extends React.PureComponent {
  state = {
    data: false,
    tags: false,
  };

  constructor(props) {
    super();

    const allEntries = props.data.craft.entries;
    const { discipline } = props.pageContext;
    const data = filterArticles(allEntries, discipline);
    const tags = findTags(allEntries);

    this.state = { data, tags };
  }

  render() {
    const seomatic = this.props.data.craft.seomatic;
    const { tags, data } = this.state;

    return (
      <Layout className="work" seo={seomatic}>
        {tags && <WorkNav data={tags} />}

        {data && <CreateTiles data={data} />}
      </Layout>
    );
  }
}

export const query = graphql`
  query {
    craft {
      seomatic(uri: "/work") {
        metaTitleContainer
        metaTagContainer
        metaLinkContainer
        metaScriptContainer
        metaJsonLdContainer
      }
      entries(section: [projects], hidden: false) {
        ... on Craft_Projects {
          hidden
          slug
          title
          previewTile {
            id
            dateUpdated
            url

            ... on Craft_UploadsVolume {
              __typename
              imageFile {
                childImageSharp {
                  fluid(maxWidth: 650) {
                    src
                    sizes
                    srcSet
                    srcWebp
                    srcSetWebp
                    aspectRatio
                  }
                }

                colors {
                  muted
                }
              }
            }
          }
          description {
            content
          }
          categories {
            title
          }
          disciplines {
            title
          }
        }
      }
    }
  }
`;
